import React, {useState , useEffect} from 'react'
import './comp.css'
import axios from 'axios'


const Modal =({active, setActive , user = null, tasks = null, padavans = null }) => {

return (
            <div className = {active ? "modal active" : "modal"}>
                <div className = {active ? "modal_content active" : "modal_content "}>
                    <p className = "modal_close" onClick= {() => setActive(false)}> X </p>

                        <div >
                <label className="words"> Title: </label>
                <br/>
                <input  type="text"
                  name="Title"
                  className = 'text_box'
                  placeholder="Title">
                  </input>
            </div>
            <div >
                <label className="words"> Date_end: </label>
                <br/>
                <input  type="date"
                  name="Date_end"
                  className = 'text_box'>
                  </input>
            </div>
            <div >
                <label className="words"> Ответственный: </label>
                <br/>
                <select className = 'text_box'  name="Responser">
                    <option value= {user.ID}>{user.username}</option>
                    {padavans.map(c => (
                    <option value= {c.ID}>{c.username}</option>
                    ))}
                </select>
            </div>
            <div >
                <label className="words"> Приоритет: </label>
                <br/>
                <select className = 'text_box'  name="Priority">
                    <option value= "H">Высокий</option>
                    <option value= "M">Средний</option>
                    <option value= "L">Низкий</option>

                </select>
            </div>
            <div >
                <label className="words"> Статус: </label>
                <br/>
                <select className = 'text_box'  name="Priority">
                    <option value= "NS">Не начато</option>
                    <option value= "P">В работе</option>
                    <option value= "D">Выполнено</option>
                    <option value= "C">Отменено</option>
                </select>
            </div>
             <div >
                <textarea className="text_box area" placeholder= {user.ID}/>
            </div>
                </div>
            </div>

)
};

export default Modal


const UpdateModal =({active, setActive , task ,user, padavans}) => {

return (
            <div className = {active ? "modal active" : "modal"}>
                <div className = {active ? "modal_content active" : "modal_content "}>
                    <p className = "modal_close" onClick= {() => setActive(false)}> X </p>

                        <div >
                <label className="words"> Title: </label>
                <br/>
                <input  type="text"
                  name="Title"
                  className = 'text_box'
                  placeholder="Title"
                  value = {task.Title}>
                  </input>
            </div>
            <div >
                <label className="words"> Date_end: </label>
                <br/>
                <input  type="date"
                  name="Date_end"
                  className = 'text_box'
                  value = {task.Date_end}>
                  </input>
            </div>
            <div >
                <label className="words"> Ответственный: </label>
                <br/>
                <select className = 'text_box'  name="Responser" value = {task.Respons}>
                    <option value= {user.ID}>{user.username}</option>
                    {padavans.map(c => (
                    <option value= {c.ID}>{c.username}</option>
                    ))}
                </select>
            </div>
            <div >
                <label className="words"> Приоритет: </label>
                <br/>
                <select className = 'text_box'  name="Priority" value = {task.Priority}>
                    <option value= "H">Высокий</option>
                    <option value= "M">Средний</option>
                    <option value= "L">Низкий</option>

                </select>
            </div>
            <div >
                <label className="words"> Статус: </label>
                <br/>
                <select className = 'text_box'  name="Priority" value = {task.Status}>
                    <option value= "NS">Не начато</option>
                    <option value= "P">В работе</option>
                    <option value= "D">Выполнено</option>
                    <option value= "C">Отменено</option>
                </select>
            </div>
             <div >
                <textarea className="text_box area" value ={task.Description}/>
            </div>
                </div>
            </div>

)
};



